.App {
  max-width: 1200px;
  margin: 0 auto;
}

.center {
  /* display: flex;
  justify-content: center; */
  margin: 0 auto;
  padding-top: 50px;
  width: 250px;
}

.file_input {
  margin-top: 50px;
  width: 100%;
}

.main {
  display: flex;

}

.table {
  padding: 25px;
  margin-bottom: 30px;
  background-color: white;
}

.info {
  width: 400px;
}

.stick {
  position: sticky;
  top: 0;
  padding: 30px 0;
}

.div {
  padding: 30px;
}

h3 {
  margin-bottom: 20px !important;
  color: blue !important;
}

.red {
  color: red;
  font-weight: 700;
  font-size: large;
}

.filter_item:hover {
  font-weight: 500;
  cursor: pointer;
  color: red;
}

.items {
  list-style-type: none;
  padding: 0
}

.block {
  margin-top: 30px;
}

.active {
  color: red !important;
  font-weight: 500;
  
}

a {
  color: black !important;
}

.table_header {
  display: flex;
  justify-content: space-between;
}

.hidden {
  display: none;
}

.button {
  cursor: pointer;
}